import React, {useState, useRef} from "react"
import {Link} from 'gatsby'
import SEO from "../components/seo"
import { Helmet } from "react-helmet"

const ListLink = props => (
  <li style={{ float: `left`, marginRight: `1rem` }}>
    <Link style={{ textDecoration: 'none', color: 'black' }} to={props.to}>{props.children}</Link>
  </li>
)

const IndexPage = () => {

  const [style,setStyle]=useState(true)

  return (
    <>
    <SEO title="Home" />
    <Helmet>
      <link rel="shortcut icon" type="image/x-icon" href="https://user-images.githubusercontent.com/71132893/103412811-4ba46f80-4bba-11eb-94b9-454fc8d16d8d.png" />
      <meta name="google-site-verification" content="K3o8Vgz6R-ISTQoiTQoKzT1t5rGgOC5V-wMf0tg00fw" />
    </Helmet>
    <div style={{width:'100vw',height:'100vh',backgroundPosition:'center',filter:'grayscale(1)',backgroundRepeat:'no-repeat',backgroundSize:'cover',}}>
      {/* backgroundImage:'url(https://user-images.githubusercontent.com/71132893/103657888-cf40d080-4fad-11eb-9229-4c9522802ffa.jpg)' */}
      <div className="index-main">Ho'space</div>
      <div className='index-intoduction'>welcome to my blog</div>
    </div>
    <nav  style={{ padding:'5vh 5vw', width:'90vw', position:'absolute', top:'90%', left:0 }}>
      <div>
        <Link to="/" style={{ position: 'relative', color: `black`, textDecoration: `none`, }}>
          {style && <div style={{ position: 'absolute', width: '20px', height: '20px',color:'black', backgroundImage: 'url(https://user-images.githubusercontent.com/71132893/103412811-4ba46f80-4bba-11eb-94b9-454fc8d16d8d.png)', backgroundSize: 'contain', backgroundRepeat: 'no-repeat' }}></div>}
          <div style={{ position: 'absolute', width: '90px',color:'black', left: '30px' }}>Ho'space</div>
        </Link>
      </div>
      <ul style={{ margin: '0', padding: '0', listStyle: `none`, float: `right` }}>
        <ListLink to="/about/">About</ListLink>
        <ListLink to="/blog/">Blog</ListLink>
        <ListLink to="/contact/">Contact</ListLink>
      </ul>
    </nav>
    </>
  )
}

export default IndexPage